/* Fonts */
@font-face {
  font-family: inter;
  src: url("/public/Inter-ExtraLight.ttf");
}

/* Variables */
:root {
  --font1: inter, sans-serif;
  --color1: rgba(0, 0, 0, 0.5);
}

/* General */
* {
  margin: 0%;
  padding: 0%;
  scroll-behavior: smooth;
  color: white;
}
/* General: Nav Sources */
.nav-source {
  scroll-margin-top: 4em;
}

/* Main Containers */
body {
  background-image: url("/public/Images/piano-double.jpg");
  background-size: 100%;

  margin: 0%;
  padding: 0%;

  font-family: var(--font1);
}
div#root {
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
}